<template>
<div>
    <el-card shadow="always" class="elcard">
            <div>{{title}}</div>
            <!-- :rules="rules" -->
            <el-form  :model="college" label-position="top"   ref="ruleForm" label-width="80px"  >
                <el-form-item label="" prop="collegeName">
                     <el-input v-model="college.collegeName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="officeName">
                     <el-input v-model="college.officeName"></el-input>
                </el-form-item>
                <el-form-item>

                        <el-button type="primary" v-if="!isUpdate" @click="submitForm('ruleForm')"></el-button>
                        <el-button type="success"  v-else  @click="submitForm('ruleForm')"></el-button>
                </el-form-item>
            </el-form>
    </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
import { MessageBox } from 'element-ui';
export default {
//import 
components:{},
props:{},
data(){
//
return{
    title:"",
    isUpdate:false,
    college: {
          collegeName: '',
          officeName: '',
    },
    rules:{
        collegeName:[
            { required: true, message: '', trigger: 'blur' }
        ],
        officeName:[
            { required: true, message: '', trigger: 'blur' }
        ]
    }
};
},
//
computed: {

},
//data
watch: {
      '$route' (to, from) {
      // ...
      if(to.path!=from.path){
            Object.assign(this.$data, this.$options.data())}
      }
},
//
methods: {
    addCollege(){
        request({
            url:'/college/add',
            method:'post',
            data:{
                collegeName:this.college.collegeName,
                officeName:this.college.officeName
            }
        }).then(res=>{
            console.log(res);
            if(res.code!=500){
                console.log("");
                this.$message({
                message: '',
                type: 'success'
                });
            }else{
                console.log("");
                this.$message({
                message: res.msg,
                type: 'error'
                });
            }
        }).catch(err=>{
            console.log(err);
        })
    },
    getCollegeById(id){
         request({
            url:`/college/get/${id}`,
            method:'get',
        }).then(res=>{
            this.college=res.data.college
        })
    },
    updateCollege(){
        request({
            url:'/college/update',
            method:'put',
            data:{
                id:this.college.id,
                collegeName:this.college.collegeName,
                officeName:this.college.officeName
            }
        }).then(res=>{
            console.log(res);
            if(res.code!=500){
                console.log("");
                this.$message({
                message: '',
                type: 'success'
                });
            }else{
                console.log("");
                this.$message({
                message: res.msg,
                type: 'error'
                });
            }
        }).catch(err=>{
            console.log(err);
        })
    },
        submitForm(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if(!this.isUpdate){
                        this.addCollege()
                    }else{
                        this.updateCollege()
                    }
                } else {
                    return false;
                }
                });
        },

},
// - this
created() {
    if(this.$route.params&&this.$route.params.id){
        this.isUpdate=true;
        this.title=""
        this.getCollegeById(this.$route.params.id)
    }


},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
</style>