<template>
<div>

        <el-card shadow="always" class="elcard">
            <el-button @click="clearFilter"></el-button>
            <el-table
            ref="filterTable"
            :data="teacherList"
            style="width: 100%">

                <el-table-column
                label=""
                width="180">
                    <template slot-scope="scope">
                        <img :src="scope.row.headerUrl"  class="header-img">
                        <span style="margin-left: 10px">{{ scope.row.name }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label=""
                width="80">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.sex }}</span>
                    </template>
                </el-table-column>


                <el-table-column
                label=""
                width="150">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.phone }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label="qq"
                width="180">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.qq }}</span>
                    </template>
                </el-table-column>


                <el-table-column
                label=""
                width="150">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.officeName }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label=""
                width="170"
                :filters="filters"
                :filter-method="filterTag"
                filter-placement="bottom-end">

                <template slot-scope="scope">
                    <el-tag
                    type="success"
                    disable-transitions>{{scope.row.college.collegeName}}</el-tag>
                </template>
                </el-table-column>

                 <el-table-column
                label="">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
                </el-table-column>
            </el-table>
        </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
//
return{
        teacherList:[
            // {
            //  id: 1,
            //  name: "",
            //  sex: "",
            //  phone: "11111111111",
            //  password: "666666",
            //  qq: "1954418428",
            //  headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
            //  officeName: "2101",
            //  isAdmin: 1,
            //  collegeId: 1,
            //  college: {
            //         id: 1,
            //         collegeName: "",
            //         officeName: "11105",
            //         classList: [],
            //         scInfoList: [],
            //         teacherList: []
            //     },
            // scInfoList: [],
            // aclass: null
            //}
        ],
        filters:[]
    }
},
//
computed: {

},
//data
watch: {
    
},
//
methods: {
        getTeacherList(){
            request({
                url:'/teacher/list',
                method:'get'
            }).then(res=>{
                this.teacherList=res.data.teacherList
                this.getFilters()
            })
        
        },
       
        getFilters(){
                for(let i=0;i<this.teacherList.length;i++){
                let college=this.teacherList[i].college;
                let value=college.id;
                let text=college.collegeName;
                this.filters.push({text,value})
                }
                var arr=this.filters
                var result = [];
                var obj = {};
                for(var i =0; i<arr.length; i++){
                if(!obj[arr[i].value]){
                    result.push(arr[i]);
                    obj[arr[i].value] = true;
                }
                }
                this.filters=result
                
        },
        clearFilter() {
        this.$refs.filterTable.clearFilter();
        },
        filterTag(value, row) {
        return row.college.id === value;
        },
        handleEdit(index, row) {
            console.log(index, row);
            this.$router.push({
                path:`teacheradd/${row.id}`,
            })
        },
        handleDelete(index, row) {
            console.log(index, row);
            this.$confirm(',,?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'error',
            }).then(() => {
                    request({
                        url:`/teacher/delete/${row.id}`,
                        method:'delete'
                    }).then(res=>{
                            if(res.code!=500){
                                this.$message({
                                type: 'success',
                                message: '!'
                                });
                                this.getTeacherList();
                            }else{
                                this.$message({
                                type: 'error',
                                message: '!'
                                });
                            }
                        }).catch(err=>{console.log(err);})
                        
                }).catch(err=>{
                    this.$message({
                        type: 'warning',
                        message: '!'
                    });
                })
        }
},
// - this
created() {
this.getTeacherList()

},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    height: 50px;
    width: 50px;
    border-radius: 25px;
    display: inline;
    vertical-align:middle;
    margin-left: 10px;
}
</style>