<template>
<div>
        <el-card shadow="always" class="elcard">
            <el-table
              :data="teacherWinningInfoList"
              style="width: 100%">

              <el-table-column
                label=""
                width="180">
                <template slot-scope="scope">
                  <span>{{ scope.row.scInfo.scName}}</span>
                </template>
              </el-table-column>

              <el-table-column
                label=""
                width="180">
                <template slot-scope="scope">
                  <span>{{ scope.row.wiName }}</span>
                </template>
              </el-table-column>
              
              <el-table-column
                label=""
                width="220">
                <template slot-scope="scope">
                  <span>{{ scope.row.description }}</span>
                </template>
              </el-table-column>
              
              <el-table-column
                label=""
                width="200">
                <template slot-scope="scope">
                  <span>{{ scope.row.student.name }}</span>
                  <img :src="scope.row.student.headerUrl" alt="" class="header-img">
                </template>
              </el-table-column>


              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)"></el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>
        </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
import  cookie from 'js-cookie'
export default {
//import 
components:{},
props:{},
data(){
//
return{
  teacher:{
        id: 1,
        name: "",
        sex: "",
        phone: "11111111111",
        password: "666666",
        qq: "1954418428",
        headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        officeName: "2101",
        isAdmin: 1,
        collegeId: 2,
        college: null,
        scInfoList: [],
        aclass: null
    },
    teacherWinningInfoList:[]
};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
  getTeacherWinningInfoList(){
      request({
        url:`/winningInfo/teacherWinningInfoList/${this.teacher.id}`,
        method:'get'
      }).then(res=>{
          this.teacherWinningInfoList=res.data.teacherWinningInfoList
      })
  },
  handleEdit(index, row){
          this.$router.push('/adminMain/wiadd/'+row.id)
  },
  handleDelete(index,row){
          this.$confirm(' ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          }).then(() => {
            request({
                url:`/winningInfo/delete/${row.id}`,
                method:'delete'
            }).then(res=>{
                this.$message({
                    type: 'success',
                    message: '!'
                });
                this.getTeacherWinningInfoList();
            }).catch(err=>{
                 this.$message({
                    type: 'error',
                    message: '!'
                });
            })
            })
  }
},
// - this
created() {
  this.getTeacherWinningInfoList()
},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    margin-left: 10px;
    height: 50px;
    width: 50px;
    border-radius: 25px;
    display: inline;
    vertical-align:middle;
}
</style>