<template>
<div style="min-height:500px;margin-bottom:10px;">
       <el-table
    height="500"
    :data="ScWinningInfo"
    style="width: 100%;margin-bottom:50px;">

        <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.scName }}</span>
        </template>
        </el-table-column>

        <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.teacher.name }}</el-tag>
            </div>
        </template>
        </el-table-column>

        <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.wiName}}</span>
        </template>
        </el-table-column>

        <el-table-column
        label=""
        width="200">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.description }}</span>
        </template>
        </el-table-column>


        <el-table-column label="">
        <template slot-scope="scope">
            <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"></el-button>
        </template>
        </el-table-column>
  </el-table>
    
</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
//
return{
       ScWinningInfo: [
        {
          id:0,
          scName:'',
          teacher:{id:1,name:''},
          wiName: '1111111111111111111',
          description:"",
        }
        ]

};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
        getScWinningInfo(){
        let id=JSON.parse(cookie.get('student')).id;
         if(id){
             request({
                 url:`/winningInfo/studentWinningInfoList/${id}`,
                 method:'get'
             }).
             then(res=>{
                    this.ScWinningInfo=res.data.ScWinningInfo
             }).catch(err=>{
                 console.log(err)
             })
         }
        },
        handleEdit(index, row) {
        console.log(index, row);
        this.$router.push('/winninginfodetails/'+row.id)
      }
      
},
// - this
created() {
   this.getScWinningInfo();
},

};
</script>

