<template>
<!-- background-color:#DDDDDD -->
<div style="min-height:500px;margin-bottom:10px;">

   <el-select v-model="scId" placeholder="" @change="scChange">
    <el-option
      v-for="item in studentScInfoList"
      :key="item.id"
      :label="item.scName"
      :value="item.id">
    </el-option>
  </el-select>

   <el-select v-model="epId" placeholder="" @change="epChange">
    <el-option
      v-for="item in examProjectList"
      :key="item.id"
      :label="item.projectName"
      :value="item.id">
    </el-option>
  </el-select>

    <div id="echarts-main" style="width: 100%;height: 600px;">
    </div>



</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"
let echarts = require('echarts');
export default {
//import 
components:{},
props:{},
data(){
//
return{
  studentId:"",
  studentScInfoList:[],
  scId:"",
  examProjectList:[],
  epId:"",
  scExamList:[],
  charts: '',
};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
  getStudentScInfoList(){
      let id=JSON.parse(cookie.get('student')).id;
         if(id){
           this.studentId=id;
             request({
                 url:`/scInfo/studentScInfoList/${id}`,
                 method:'get'
             }).
             then(res=>{
                    this.studentScInfoList=res.data.studentScInfoList
             }).catch(err=>{
                 console.log(err)
             })
         }
  },
  getExamProjectList(scId){
    request({
      url:`/examProject/list/${scId}`,
      method:'get'
    }).then(res=>{
      this.examProjectList=res.data.examProjectList
    }).catch(err=>{
      console.log(err);
      
    })
  },
  scChange(scId){
    this.epId=""
    this.examProjectList=[];
    echarts.init(document.getElementById('echarts-main')).dispose();

    this.getExamProjectList(scId)
  },
  epChange(epId){
    this.scExamList=[];
      echarts.init(document.getElementById('echarts-main')).dispose();
    request({
      url:`/scExam/list/${this.studentId}/${this.scId}/${epId}`,
      method:'get'
    }).then(res=>{
      if(res.code!=500){
              this.scExamList=res.data.scExamList;
              this.drawing();
      }else{
         this.$message({
              showClose: true,
              message: '',
              type: 'warning'
          });
      }
    }).catch(err=>{
      console.log(err);
    })
  },
  drawing(){
    let titleText="";
    for(let i=0;i<this.studentScInfoList.length;i++){
      if(this.studentScInfoList[i].id==this.scId){
          titleText+=this.studentScInfoList[i].scName+"";
      }
    }
    for(let j=0;j<this.examProjectList.length;j++){
      if(this.examProjectList[j].id==this.epId){
          titleText+=this.examProjectList[j].projectName+"";
      }
    }
    
    let xAxisData=[];
    for(let i=0;i<this.scExamList.length;i++){
      xAxisData.push(""+this.scExamList[i].examIndex+"")
    }
    let seriesData=[];
    for(let i=0;i<this.scExamList.length;i++){
      seriesData.push(this.scExamList[i].score)
    }
     // domecharts
    let myChart = echarts.init(document.getElementById('echarts-main'),'dark');
    // 
    myChart.setOption({
      title: {
        text: titleText,//
        x: 'center'//
      },
      tooltip: {//
        axisPointer: {
            type: 'shadow'
        }
      },
    
      xAxis: {
          name:'',
          type: 'category',
          data: xAxisData,
          
      },
      yAxis: {
          name:'',
          type: 'value',
          min:0,//
          max:100, //
      },
      series: [{
          data: seriesData,
          type: 'line',
          itemStyle:{ 
                normal:{ color: "#d14a61" } //
          }, 
          lineStyle:{ 
                normal:{ color: "green"  }//
          },
          label: {//
            normal: {
                show: true,
                position: 'top',
                color: "#d14a61",
                fontWeight: "bold"
              },
          },
          
      }]
    });

  }

},
// - this
created() {
  this.getStudentScInfoList();
},
// - DOM
mounted() {
 
},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
</style>