<template>
<div>
        <el-card shadow="always" class="elcard">
            <el-table
              :data="teacherScInfoList"
              style="width: 100%">
              <el-table-column
                label=""
                width="180">
                <template slot-scope="scope">
                  <span style="margin-left: 10px">{{ scope.row.scName }}</span>
                </template>
              </el-table-column>

              <el-table-column
                label=""
                width="180">
                <template slot-scope="scope">
                  <span style="margin-left: 10px">{{ scope.row.scSponsor }}</span>
                </template>
              </el-table-column>
              
              <el-table-column
                label=""
                width="180">
                <template slot-scope="scope">
                  <span style="margin-left: 10px">{{ scope.row.description }}</span>
                </template>
              </el-table-column>
              
              <el-table-column
                label=""
                width="200">
                <template slot-scope="scope">
                  <i class="el-icon-time"></i>
                  <span style="margin-left: 10px">{{ scope.row.startTime }}</span>
                </template>
              </el-table-column>

              <el-table-column
                label=""
                width="200">
                <template slot-scope="scope">
                  <i class="el-icon-time"></i>
                  <span style="margin-left: 10px">{{ scope.row.endTime }}</span>
                </template>
              </el-table-column>

              <el-table-column label="">
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)"></el-button>
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
              </el-table-column>
            </el-table>
        </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
//
return{
  teacher:{
        id: 1,
        name: "",
        sex: "",
        phone: "11111111111",
        password: "666666",
        qq: "1954418428",
        headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
        officeName: "2101",
        isAdmin: 1,
        collegeId: 2,
        college: null,
        scInfoList: [],
        aclass: null
    },
  teacherScInfoList:[]
};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
  getTeacherScInfoList(){
      request({
        url:`/scInfo/teacherScInfoList/${this.teacher.id}`,
        method:'get'
      }).then(res=>{
        this.teacherScInfoList=res.data.teacherScInfoList
      })
  },
    handleEdit(index, row){
          this.$router.push('/adminMain/scinfoadd/'+row.id)
    },
    handleDelete(index,row){
          this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
          }).then(() => {
            request({
                url:`/scInfo/delete/${row.id}`,
                method:'delete'
            }).then(res=>{
                this.$message({
                    type: 'success',
                    message: '!'
                });
                this.getTeacherScInfoList();
            }).catch(err=>{
                 this.$message({
                    type: 'error',
                    message: '!'
                });
            })
            })
  }
},
// - this
created() {
  this.getTeacherScInfoList()
},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
</style>