<template>
<div>
        <el-card shadow="always" class="elcard">
            <div>{{title}}</div>
            <el-form :model="teacher" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
                <el-form-item label=":" prop="headerUrl">
                    <img :src="teacher.headerUrl" alt="" class="header-img">
                    <el-upload
                    ref="upload"
                    name="resources"
                    action="http://localhost:9000/oss/upload"
                    :on-success="uploadFileSuccess"
                    :on-error="uploadFileError"
                    multiple
                    accept=".jpg,.png"
                    >
                    <el-button size="small" type="primary"></el-button>
                    <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
                    </el-upload>
                </el-form-item>

                <el-form-item label=":" prop="name">
                    <el-input v-model="teacher.name"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="sex">
                    <el-radio v-model="teacher.sex" label=""></el-radio>
                    <el-radio v-model="teacher.sex" label=""></el-radio>
                </el-form-item>
                <el-form-item label=":" prop="phone">
                    <el-input v-model="teacher.phone"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="password">
                    <el-input v-model="teacher.password"></el-input>
                </el-form-item>
                <el-form-item label="QQ:" prop="qq">
                    <el-input v-model="teacher.qq"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="officeName">
                    <el-input v-model="teacher.officeName"></el-input>
                </el-form-item>
                <el-form-item label=":" prop="isAdmin">
                        <el-switch
                            v-model="teacher.isAdmin"
                            active-color="#13ce66"
                            inactive-color="#ff4949"
                            :active-value="1"
                            :inactive-value="0">
                        </el-switch>
                </el-form-item>
                  <el-form-item label=":" prop="collegeId">
                        <el-select v-model="teacher.collegeId" placeholder="">
                            <el-option
                            v-for="item in collegeList"
                            :key="item.id"
                            :label="item.collegeName"
                            :value="item.id">
                             </el-option>
                        </el-select>
                       
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-if="!isUpdate" @click="submitForm('ruleForm')"></el-button>
                    <el-button type="success" v-else  @click="submitForm('ruleForm')"></el-button>
                    <el-button @click="resetForm('ruleForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-card>

</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
//
return{
    title:'',
    isUpdate:false,
    isSuccessSubmit:false,
    collegeList:[],
    teacher: {
            headerUrl: 'https://dss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=2561659095,299912888&fm=26&gp=0.jpg',
            name: '',
            sex: '',
            phone: '13142211138',
            password: '123456789',
            qq: '1954418428',
            officeName: '1101',
            isAdmin: 0,
            collegeId:''
    },
    ObjectName:'',
    rules: {
          headerUrl: [
            { required: true, message: '', trigger: 'blur' },
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
          ],
          sex: [
            { required: true, message: '', trigger: 'change' }
          ],
          phone: [
            { required: true, message: '', trigger: 'change' },
            { min: 11, max: 11, message: '11', trigger: 'blur' }
          ],
          password:[
            {required: true, message: '', trigger: 'blur' },
            { min: 8, max: 16, message: '816', trigger: 'blur' }
          ],
          qq: [
            {  required: true, message: 'QQ', trigger: 'blur' },
            { min: 5, max: 14, message: 'QQ514', trigger: 'blur' }
          ],
          officeName: [
            { required: true, message: '', trigger: 'blur' }
          ],
          isAdmin: [
            { required: true, message: '', trigger: 'blur' }
          ],
          collegeId: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }

};
},
//
computed: {

},
//data
watch: {
'$route' (to, from) {
      // ...
      if(to.path!=from.path){
            Object.assign(this.$data, this.$options.data())}
        }
},
//
methods: {
    getCollgeList(){
        request({
            url:'/college/list'
        }).then(res=>{
            this.collegeList=res.data.collegeList    
        })
    },
    addTeacher(){
      request({
        url:'/teacher/add',
        method:'post',
        data:{
            headerUrl: this.teacher.headerUrl,
            name:this.teacher.name,
            sex: this.teacher.sex,
            phone: this.teacher.phone,
            password: this.teacher.password,
            qq: this.teacher.qq,
            officeName: this.teacher.officeName,
            isAdmin: this.teacher.isAdmin,
            collegeId:this.teacher.collegeId,
        }
      }).then(res=>{
         if(res.code!=500){
              console.log("");
              this.$message({
              message: '',
              type: 'success'
              });
              this.isSuccessSubmit=true;
          }else{
              console.log("");
              this.$message({
              message: res.msg,
              type: 'error'
              });
          }
      })
    },
    updateTeacher(){
        request({
        url:'/teacher/update',
        method:'put',
        data:{
            id:this.teacher.id,
            headerUrl: this.teacher.headerUrl,
            name:this.teacher.name,
            sex: this.teacher.sex,
            phone: this.teacher.phone,
            password: this.teacher.password,
            qq: this.teacher.qq,
            officeName: this.teacher.officeName,
            isAdmin: this.teacher.isAdmin,
            collegeId:this.teacher.collegeId,
        }
      }).then(res=>{
         if(res.code!=500){
              console.log("");
              this.$message({
              message: '',
              type: 'success'
              });
              this.isSuccessSubmit=true;
          }else{
              console.log("");
              this.$message({
              message: res.msg,
              type: 'error'
              });
          }
      })
    },
    getTeacher(id){
      request({
        url:`/teacher/get/${id}`
      }).then(res=>{
        this.teacher=res.data.teacher
      }).catch(err=>{
        console.log(err);
        
      })
    },
       uploadFileSuccess(response, file, fileList){
        if(this.ObjectName){
            console.log("");
            
            this.fileRemove(this.ObjectName)
        }
        this.$refs.upload.clearFiles(); 
        console.log("",response)
        let name=response.data.urlList[0].fileName;//
        let ObjectName=response.data.urlList[0].resourceName;//oss
        this.ObjectName=ObjectName;
        let url=response.data.urlList[0].resourceUrl;//oss
        this.teacher.headerUrl=url;
    },
    uploadFileError(err, file, fileList){
        console.log("",response)
         this.$refs.upload.clearFiles(); //
        this.$message({  //
          type: 'error',
          message: '',
          center: true,
          duration: 1000
        });
    },
    fileRemove(ObjectName) {
        console.log("");
        request({
            url:'/oss/delete',
            method:'delete',
            params:{
                ObjectName:ObjectName
            }
        }).then(res=>{
            this.ObjectName=''
        }).catch(err=>{
        })
    },

      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(!this.isUpdate){
              this.addTeacher();
            }else{
              this.updateTeacher();
            }
            
          } else {
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
},
// - this
created() {
  this.getCollgeList();
  
  if(this.$route.params&&this.$route.params.id){
    this.title=""
      this.isUpdate=true;
      this.getTeacher(this.$route.params.id)
  }
},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {
    //  if(this.ObjectName&&!this.isSuccessSubmit){
    //         console.log("");
    //         this.fileRemove(this.ObjectName)
    //     }
},// - 
beforeDestroy() {
     if(this.ObjectName&&!this.isSuccessSubmit){
            console.log("");
            this.fileRemove(this.ObjectName)
    }
},// - 
destroyed() {
},
activated() {},//keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    height: 50px;
    width: 50px;
    border-radius: 25px;
}
</style>