<template>
<div style="min-height:500px;margin-bottom:10px;">
    <el-table
    height="500"
    :data="ScApplyInfo"
    style="width: 100%;margin-bottom:50px;">

        <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.scName }}</span>
        </template>
        </el-table-column>

        <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
                <el-tag size="medium">{{ scope.row.teacher.name }}</el-tag>
            </div>
        </template>
        </el-table-column>

        <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
            <span style="margin-left: 10px">{{ scope.row.college.collegeName}}</span>
        </template>
        </el-table-column>

        <el-table-column
        label=""
        width="200">
        <template slot-scope="scope">
            <i class="el-icon-time"></i>
            <span style="margin-left: 10px">{{ scope.row.createTime }}</span>
        </template>
        </el-table-column>

    <el-table-column
        label=""
        width="180">
        <template slot-scope="scope">
            <div slot="reference" class="name-wrapper">
                <el-tag v-if="scope.row.status==1" type="success" size="medium"></el-tag>
                <el-tag v-else type="danger" size="medium"></el-tag>

            </div>
        </template>
    </el-table-column>
  

        <el-table-column label="">
        <template slot-scope="scope">
            <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"></el-button>
            <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
        </el-table-column>
  </el-table>


</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
//
return{
    ScApplyInfo: [
        {
          id:0,
          scId: 0,
          scName:'',
          teacher:{id:1,name:''},
          college:{id:1,collegeName:''},
          createTime: '2016-05-02',
          status:1
        }
        ]
};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
        getScApplyInfo(){
         let id=JSON.parse(cookie.get('student')).id;
         if(id){
            request({
                url:`/scApply/studentScApplyList/${id}`,
                method:'get'
            }).then(res=>{
                this.ScApplyInfo=res.data.ScApplyInfo
            }).catch(err=>{
                
            })
         }
           
        },

     handleEdit(index, row) {
        console.log(index, row);
         this.$router.push({
            name: 'apply',
            params: {
            collegeId:row.college.id,
            scId:row.scId,
            isUpdate:true,
            applyId:row.id
            }
        })
      },
      handleDelete(index, row) {
        console.log(index, row);
        console.log("id",row.id)

         this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            console.log("")
            request({
                url:`/scApply/delete/${row.id}`,
                method:'delete'
            }).then(res=>{
                this.$message({
                    type: 'success',
                    message: '!'
                });
                this.getScApplyInfo();
            }).catch(err=>{
                 this.$message({
                    type: 'error',
                    message: '!'
                });
            })
         
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });  
        });
      }

},
// - this
created() {
this.getScApplyInfo();
}
};
</script>

<style scoped>
</style>