<template>
<div>
    <el-card shadow="hover" style="min-height:500px;margin-bottom:50px">
    
        <el-form :model="formData"  :rules="rules" ref="ruleForm" label-width="100px" style="width:500px" :hide-required-asterisk="false">
        
        <div class="tilte">{{titleText}}</div>
        <el-form-item label=":">
            <el-select v-model="formData.collegeId" placeholder="" @change="changeSelect" :disabled="!isApply">
            <el-option
                v-for="(item,index) in collegeList"
                :key="index"
                :label="item.collegeName"
                :value="item.id"
            />
            </el-select>
        </el-form-item>
        <el-form-item label=":" prop="scId" >
            <el-select v-model="formData.scId" placeholder="" @change="refreshTeacher" :disabled="!isApply">
            <el-option
                v-for="(item,index) in scInfoList"
                :key="index"
                :label="item.scName"
                :value="item.id"
            />
            </el-select>
            <el-button style="margin-left:5px" v-if="formData.scId" @click="lookCptDetails" type="success" icon="el-icon-view" circle></el-button>
        </el-form-item>

        <el-form-item v-if="teacher" label=":">
            <el-link  target="_blank" :underline="false" style="font-size:20px">
            {{teacher.name}}
            <img class="teacher-header" :src="teacher.headerUrl"/>
            </el-link>
        </el-form-item>

        <el-form-item label=":">
            <el-input v-model="formData.student.name" disabled></el-input>
        </el-form-item>

        <el-form-item label=":">
            <el-input v-model="formData.student.cdCard" disabled></el-input>
        </el-form-item>

        <el-form-item>
            <el-upload
                name="resources"
                action="http://localhost:9000/oss/upload"
                :on-success="uploadFileSuccess"
                :on-error="uploadFileError"
                :on-remove="fileRemove"
                :before-remove="beforeFileRemove"
                :on-preview="handlePreview"
                multiple
                :limit="1"
                :on-exceed="handleExceed"
                :file-list="fileList">
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">5MB,20MB</div>
            </el-upload>
        </el-form-item>
        <el-form-item>
             <el-button v-if="isApply" type="primary" @click="apply('ruleForm')" round></el-button>
             <el-button v-else type="success" @click="update('ruleForm')" round></el-button>
        </el-form-item>
    </el-form>

    </el-card>
   
</div>

</template>

<script>
import request from "../../../utils/request"
import  cookie from 'js-cookie'

export default {
//import 
components:{},
props:{},
data(){
//
return{
    titleText:'',
    isApply:true,
    isFirstload:true,
    collegeList:[],
    scInfoList:[],
    teacher:'',
    formData:{
        collegeId:'',
        scId:'',
        student:{},
        applyFile:''
    },
    fileList: [
       
    ],
    rules: {
          scId: [
            { required: true, message: '', trigger: 'change' },
          ]
    }


};
},
//
computed: {
},
//data
watch: {
},
//
// - this
created() {
    this.getBaseInfo();
    let {scId,collegeId,isUpdate,applyId}=this.$route.params;
    //this.$route.params.applyId
    if(scId&&collegeId){
        //sc_id 
        // this.isApply=true;
        this.isCheckScInfoInto=true;
        this.formData.scId=scId;
        this.formData.collegeId=collegeId;
    }else{
        //sc_id 
    }
    //isUpdate 
    if(isUpdate){
        this.isApply=false;
        this.titleText=""
        request({
            url:`/scApply/get/${applyId}`,
            method:'get'
        }).then(res=>{
            let url=res.data.scApply.applyFile//oss
            let name=res.data.scApply.applyFile.slice(97);//
            let ObjectName=res.data.scApply.applyFile.slice(54);//oss
            this.formData.applyFile=url;
            let obj={
                name,
                url,
                ObjectName
            }
            this.fileList.push(obj)
        }).catch(err=>{
            console.log(err);
        })
    }
},
methods: {
    getBaseInfo(){
        //  
        request({
            url:'/college/collegeList',
            method:'get'
        }).then(res=>{
            console.log(res.data.collegeList)
            this.collegeList=res.data.collegeList;
            this.changeSelect();
            this.isFirstload=false;
        }).catch(err=>{
        })

        let studentInfo=cookie.get("student");
        if(studentInfo){
            this.formData.student = JSON.parse(studentInfo)
        }
    },
    changeSelect() {
        if(!this.isFirstload){
            this.formData.scId = ''
            this.teacher=''
        }
          for(let i=0;i<this.collegeList.length;i++){
              if(this.formData.collegeId==this.collegeList[i].id){
                  console.log("collegeList[i].id",this.collegeList[i].id);
                  this.scInfoList=this.collegeList[i].scInfoList;
              }
          }
          this.refreshTeacher();
    },
    refreshTeacher(){
        for(let i=0;i<this.scInfoList.length;i++){
            if(this.formData.scId==this.scInfoList[i].id){
                this.teacher=this.scInfoList[i].teacher;
            }
        }
    },
    uploadFileSuccess(response, file, fileList){
        console.log("",response)
        let name=response.data.urlList[0].fileName;//
        let ObjectName=response.data.urlList[0].resourceName;//oss
        let url=response.data.urlList[0].resourceUrl;//oss
        this.formData.applyFile=url;
        let obj={
                name,
                url,
                ObjectName
        }
        this.fileList.push(obj)
    },
    uploadFileError(err, file, fileList){
        console.log("",response)
    },
    beforeFileRemove(file, fileList) {
        console.log("")
        return this.$confirm(` ${ file.name }`);
    },
    fileRemove(file, fileList) {
        console.log(this.fileList[0].ObjectName);
        
        request({
            url:'/oss/delete',
            method:'delete',
            params:{
                ObjectName:this.fileList[0].ObjectName
            }
        }).then(res=>{
            this.fileList=[]
            this.formData.applyFile=''
        }).catch(err=>{
        })
    },
    handlePreview(file) {
        console.log()
        console.log("handlePreview",file);
    },
    handleExceed(files, fileList) {
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
    },
    apply(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.formData.applyFile){
                request({
                    url:'/scApply/apply',
                    method:'post',
                    data:{
                        scId:this.formData.scId,
                        studentId:this.formData.student.id,
                        applyFile:this.formData.applyFile,
                        status:0
                    }
                }).then(res=>{
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                     this.$router.push({
                        name: 'myapplylist',
                    })
                }).catch(err=>{
                    this.$message.error('');
                })
            }else{
                 this.$message({
                        message: '',
                        type: 'warning'
                    });
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
    update(formName){
        let applyId=this.$route.params.applyId;
         this.$refs[formName].validate((valid) => {
          if (valid) {
            if(this.formData.applyFile){
                request({
                    url:'/scApply/update',
                    method:'put',
                    data:{
                        id:applyId,
                        scId:this.formData.scId,
                        studentId:this.formData.student.id,
                        applyFile:this.formData.applyFile,
                        status:0
                    }
                }).then(res=>{
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                     this.$router.push({
                        name: 'myapplylist',
                    })
                }).catch(err=>{
                    this.$message.error('');
                })
            }else{
                 this.$message({
                        message: '',
                        type: 'warning'
                    });
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
    },
    lookCptDetails(){
        this.$router.push(`/cptdetails/${this.formData.scId}`)
    },
},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
.tilte{
    margin-bottom: 10px;
}
.teacher-header{
    display: inline-block;
    height: 40px;
    width: 40px;
    border-radius: 20px;
    vertical-align:middle
}
</style>