<template>
<div>
    <el-card shadow="always" class="elcard">
            <div></div>
            <el-table
            :data="collegeList"
            border
            style="width: 100%">
                <el-table-column
                label=""
                width="170">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.collegeName }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label=""
                width="180">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px">{{ scope.row.officeName }}</span>
                    </template>
                </el-table-column>

                <el-table-column
                label="">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)"></el-button>
                    <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
                </el-table-column>
            </el-table>
    </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
//
return{
    collegeList: [{
        collegeName: '',
        officeName: ' 1518 '
    }, {
        collegeName: '',
        officeName: ' 1517 '
    }, {
        collegeName: '',
        officeName: ' 1519 '
    }]
};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
       getCollegeList(){
                request({
                url:'/college/list',
                method:'get'
                }).then(res=>{
                        this.collegeList=res.data.collegeList
                }).catch(err=>{
                })
        },
        handleEdit(index, row) {
            console.log(index, row);
            this.$router.push({
                path:`collegeadd/${row.id}`,
            })
        },
        handleDelete(index, row) {
            console.log(index, row);
            this.$confirm(',,?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'error',
            }).then(() => {
                    request({
                        url:`/college/delete/${row.id}`,
                        method:'delete'
                    }).then(res=>{
                            if(res.code!=500){
                                this.$message({
                                type: 'success',
                                message: '!'
                                });
                                this.getCollegeList()
                            }else{
                                this.$message({
                                type: 'error',
                                message: '!'
                                });
                            }
                        }).catch(err=>{console.log(err);})
                        
                }).catch(err=>{
                    this.$message({
                        type: 'warning',
                        message: '!'
                    });
                })
        }
},
// - this
created() {
this.getCollegeList()
},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {
},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
</style>