<template>
<div>
        <el-card shadow="always" class="elcard">
            <div></div>
                <el-form :model="classData" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">

                <el-form-item label=":" prop="collegeId">
                        <el-select v-model="classData.collegeId" placeholder="" @change="refreshTeacherList">
                            <el-option
                            v-for="item in collegeList"
                            :key="item.id"
                            :label="item.collegeName"
                            :value="item.id">
                             </el-option>
                        </el-select>
                </el-form-item>
                <el-form-item label=":" prop="teacherId">
                        <el-select v-model="classData.teacherId" placeholder="">
                            <el-option
                            v-for="item in teacherList"
                            :key="item.id"
                            :label="item.name"
                            :value="item.id">
                             </el-option>
                        </el-select>
                </el-form-item>

                <el-form-item label=":" prop="className">
                    <el-input v-model="classData.className"></el-input>
                </el-form-item>
                
                <el-form-item>
                    <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                    <el-button @click="resetForm('ruleForm')"></el-button>
                </el-form-item>
            </el-form>
        </el-card>

</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
//
return{
        isUpdate:false,
        collegeList:[],
        teacherList:[],
        classData:{
                className:'',
                collegeId:'',
                teacherId:''
        },
        rules: {
          className: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
          ],
          collegeId: [
            { required: true, message: '', trigger: 'blur' }
          ],
          teacherId: [
            { required: true, message: '', trigger: 'blur' }
        ]
        }
};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
        addClass(){
                request({
                url:'/class/add',
                method:'post',
                data:{
                        className:this.classData.className,
                        collegeId:this.classData.collegeId,
                        teacherId:this.classData.teacherId,
                }
                }).then(res=>{
                console.log(res);
                if(res.code!=500){
                        console.log("");
                        this.$message({
                        message: '',
                        type: 'success'
                        });
                }else{
                        console.log("");
                        this.$message({
                        message: res.msg,
                        type: 'error'
                        });
                }
                }).catch(err=>{
                console.log(err);
                })
        },
        getCollegeList(){
                request({
                url:'/college/collegeTeacherList',
                method:'get'
                }).then(res=>{
                        this.collegeList=res.data.collegeList
                }).catch(err=>{
                })
        },
        refreshTeacherList(){
                for(let i=0;i<this.collegeList.length;i++){
                if(this.classData.collegeId==this.collegeList[i].id){
                this.teacherList=this.collegeList[i].teacherList;
        }
        }},
        submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.addClass();
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
        

},
// - this
created() {
        this.getCollegeList()
},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
</style>