<template>
<div>
    <div style="min-height:500px;">
        <el-select v-model="collegeId" placeholder="" @change="selectedChange" style="margin:10px 0px;">
            <el-option
            v-for="item in collegeList"
            :key="item.id"
            :label="item.collegeName"
            :value="item.id"
            >
            </el-option>
        </el-select>

        <div v-if="scInfoListLength!=0" class="sc-list">
            <el-card class="sc-item"   v-for="item in scInfoList" :key="item.id">
            <img src="https://shadow.elemecdn.com/app/element/hamburger.9cf7b091-55e9-11e9-a976-7f4d0b07eef6.png" class="image">
            <div>
                <span class="sc-name"> {{item.scName}}</span>
                <div style="position:relative">
                    <span class="sc-createtime">{{item.createTime}}</span>
                    <el-link class="look-details" :underline="false" type="primary" @click="lookDetails(item.id)"></el-link>
                </div>
            </div>
            </el-card>
        </div>
        <div v-else>
            <div>
                
            </div>
        </div>


        

    </div>

</div>
</template>

<script>
import request from "../../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
//
return{
    collegeId:'',
    collegeList:[
        {
            id:1,
            collegeName:'',
            classList:[
                    {
                        id: 1,
                        className: "2018-1",
                        collegeId: 1,
                        teacherId: 1,
                        studentList: []
                    }
            ],
            scInfoList:[
                    {
                        id: 1,
                        scName: "",
                        scSponsor: "1",
                        collegeId: 1,
                        college: null,
                        description: "1111111111111111",
                        teacherId: 1,
                        teacher: {
                            id: 1,
                            name: "",
                            sex: "",
                            phone: "11111111111",
                            password: "666666",
                            qq: "1954418428",
                            headerUrl: "https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg",
                            officeName: "2101",
                            isAdmin: 1,
                            collegeId: 1,
                            scInfoList: [],
                            aclass: null
                        },
                        status: 1,
                        startTime: "2020-11-06 08:00:00",
                        endTime: "2020-12-06 08:00:00",
                        createTime: "2020-11-06 20:17:12",
                        modifyTime: "2020-11-06 20:17:12"
            }]
        }
    ],
    scInfoList:[],
    scInfoListLength:0

};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
    getBaseInfo(){
        //  
        request({
            url:'/college/collegeList',
            method:'get'
        }).then(res=>{
            console.log(":",res.data.collegeList)
            this.collegeList=res.data.collegeList;
            this.selectedChange();
        }).catch(err=>{
        })
    },
    selectedChange(){
        for(let i=0;i<this.collegeList.length;i++){
            if(this.collegeId==this.collegeList[i].id){
                this.scInfoList=this.collegeList[i].scInfoList;
            }
        }
        this.scInfoListLength=this.scInfoList.length;
        //
        this.scInfoList.sort(this.compare("createTime"));
    },
    lookDetails(sc_id){
        this.$router.push(`/cptdetails/${sc_id}`)
    },
    //
    compare(createTime){
        return function(a,b){
            var value1 = a[createTime];
            var value2 = b[createTime];
            return Date.parse(value2)-Date.parse(value1)
        }
    }
    

},
// - this
created() {
    this.getBaseInfo();
    let {collegeId}=this.$route.params;
    // console.log(collegeId)
    if(collegeId){
        this.collegeId=collegeId;
    }else{
        this.collegeId=this.collegeList[0].id;
    }
},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
.sc-list{
    display: flex;
    justify-content:space-evenly;
    flex-wrap: wrap;
}
.sc-list:after {
    content: "";
    flex: auto;
 }

.sc-item{
    width: 340px;
    font-size: 18px;
    margin:0  1.5px 20px 1.5px ;
}
.sc-item img{
    width: 300px;
    height: 260px;
}

.sc-item .sc-name{
    margin: 10px 0;
    display: block;
}
.sc-item .look-details{
position: absolute;
top: 0;
right: 0;
font-size: 18px;
}

</style>