<template>
<div style="min-height:500px;margin-bottom:10px;">
    <div style="width:500px;">
            <el-form ref="form" :model="studentInfo" label-width="80px">
                <el-form-item label=":">
                    <img :src="studentInfo.headerUrl" style="height:80px;width:80px;border-radius:40px"/>
                </el-form-item>

                <el-form-item label=":">
                    <el-input v-model="studentInfo.name"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio v-model="studentInfo.sex" label=""></el-radio>
                    <el-radio v-model="studentInfo.sex" label=""></el-radio>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="studentInfo.phone" :disabled="!isUpdate"></el-input>
                    <span @click="dialogVisible=true"></span>
                </el-form-item>
                <el-form-item label="" >
                    <el-input v-model="studentInfo.password" :type="passwordShowType" :disabled="!isUpdate"></el-input>
                    <a class="iconfont icon-chakan" @click="showPassword"></a>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="studentInfo.cdCard" :disabled="!isUpdate"></el-input>
                </el-form-item>
                <el-form-item label="qq">
                    <el-input v-model="studentInfo.qq"></el-input>
                </el-form-item>
               
                <el-form-item label="">
                    <el-select v-model="studentInfo.collegeId" @change="collegeChange">
                        <el-option :label="item.collegeName" :value="item.id" v-for="item in collegeList" :key="item.id" ></el-option>
                    </el-select>

                    <el-select v-model="studentInfo.classId">
                        <el-option :label="item.className" :value="item.id" v-for="item in classList" :key="item.id"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="updateStudentInfo"></el-button>
                </el-form-item>
             </el-form>
            </div>

            <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="30%"
            center>

            <div>:{{studentInfo.phone}} </div>
            <div>
                <input type="text" v-model="inputCode">
                <button @click="sendCode"></button>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button @click="closeDialog"> </el-button>
                <el-button type="primary" @click="validationPhoneCode"> </el-button>
            </span>
            </el-dialog>


</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
//
return{
    activeName: 'first',
    isUpdate:false,
    passwordShowType:'password',
    dialogVisible:false,
    studentPhoneCode:'',//
    inputCode:'',//
    studentInfo:{
        id:'1',
        name:'',
        sex:'',
        phone:'13142211138',
        password:'123456789',
        cdCard:'430121200011102816',
        qq:'1954418428',
        headerUrl:'https://cptmanagement-fzy.oss-cn-beijing.aliyuncs.com/2020-11-06/4735f0fa36b74bd7b34f6a90aa9da836QQ.jpg',
        collegeId:2,
        classId:3
    },
    collegeList:[
            // {id:1,collegeName:'',classList:[{id:1,className:'1'},{id:2,className:'2'},{id:3,className:'3'},{id:4,className:'1'},{id:4,className:'2'}]},
            // {id:2,collegeName:'',classList:[{id:1,className:'1'},{id:2,className:'2'},{id:3,className:'3'},{id:4,className:'4'}]},
            // {id:3,collegeName:'',classList:[{id:1,className:'1'},{id:2,className:'2'},{id:3,className:'3'},{id:4,className:'4'}]}
    ],
    classList:[]
};
},
//
computed: {

},
//data
watch: {
// 'studentInfo.collegeId':function(){
//     this.refreshClassList();
// }
},
//
methods: {
        getCollegeAndClassList(){
            // 
            request({
                url:`/college/collegeList`,
                method:'get',
            }).then(res=>{
                this.collegeList=res.data.collegeList
                this.refreshClassList()
            }).catch(err=>{
                console.log(err)
            })
        },
        getStudentInfo(){
         let id=JSON.parse(cookie.get('student')).id;
        //  console.log("id",id);
            request({
                url:`/student/get/${id}`,
                method:'get'
            }).then(res=>{
                this.studentInfo=res.data.studentInfo
                this.refreshClassList();
            }).catch(err=>{
            })

        },
        handleClick(tab, event) {
            console.log(tab, event);
        },
        collegeChange(v){
               console.log(v);
               this.refreshClassList();
               this.studentInfo.classId=this.classList[0].id
        },
        refreshClassList(){
             for(let i=0;i<this.collegeList.length;i++){
                if(this.collegeList[i].id==this.studentInfo.collegeId){
                     this.classList=this.collegeList[i].classList;
                    break;
                }
            }
        },
        showPassword(){
            if(this.passwordShowType=='password'){
                this.passwordShowType='input';
            }else{
                this.passwordShowType='password';
            }
        },
        sendCode(){
            console.log("")
            //
            request({
                url:`/sms/send/${this.studentInfo.phone}`,
                method:'get'
            }).then(res=>{
                this.studentPhoneCode=res.data.code;
            }).catch(err=>{
                console.log(err);
            })

        },
        validationPhoneCode(){
            if(this.inputCode == this.studentPhoneCode && this.studentPhoneCode!=""){
                this.isUpdate=true;
                 this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                });
            }else{
                this.inputCode=""
                 this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
            }
            this.dialogVisible=false;
            
        },
        closeDialog(){
            this.inputCode="";
            this.dialogVisible=false;
        },

        updateStudentInfo(){
            let {id,name,sex,phone,password,cdCard,qq,headerUrl,collegeId,classId}=this.studentInfo
            request({
                url:'/student/update',
                method:'put',
                data:{
                    id,name,sex,phone,password,cdCard,qq,headerUrl,collegeId,classId
                }
                    
            }).then(res=>{
              if(res.code!=500){
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                });
                this.getStudentInfo();
              }else{
                this.$message({
                    showClose: true,
                    message: res.msg,
                    type: 'error'
                });
              }
                
            }).catch(err=>{
                console.log(err);
                
            })

        }
},
// - this
created() {
this.getCollegeAndClassList();
this.refreshClassList();



this.getStudentInfo();
},
};
</script>

<style scoped>
.tab-list{
    width: 1280px;
    margin: 10px auto;
}
.el-form-item{
    margin: 5px;
}
</style>