<template>
<div>
          <el-card shadow="always" class="elcard">
              <div>
                {{this.college.collegeName}}{{teacher.aclass.className}}
              </div>

            <el-table
            :data="classStudentList"
            border
            style="width: 100%">
                <el-table-column
                label=""
                width="220">
                    <template slot-scope="scope">
                        <img :src="scope.row.headerUrl"  class="header-img">
                        <span>{{ scope.row.name }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                width="100">
                    <template slot-scope="scope">
                        <span>{{ scope.row.sex }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                width="150">
                    <template slot-scope="scope">
                        <span>{{ scope.row.phone }}</span>
                    </template>
                </el-table-column>
                <el-table-column
                label=""
                width="120">
                    <template slot-scope="scope">
                        <span>{{ scope.row.password}}</span>
                    </template>
                </el-table-column>
                 <el-table-column
                label=""
                width="180">
                    <template slot-scope="scope">
                        <span>{{ scope.row.cdCard }}</span>
                    </template>
                </el-table-column>


                <el-table-column
                label="">
                <template slot-scope="scope">
                    <el-button
                    size="mini"
                    @click="openUpdateStudentDialog(scope.row.id)"></el-button>

                    <el-button
                    size="mini"
                    type="primary"
                    @click="showStudentApplyList(scope.row.id)"></el-button>

                    <el-button
                    size="mini"
                    type="warning"
                    @click="doStudentExam(scope.row.id)"></el-button>

                    <el-button
                    size="mini"
                    type="danger"
                    @click="Delete(scope.row.id)"></el-button>
                </template>
                </el-table-column>
            </el-table>


            <el-dialog title="" :visible.sync="showUpdateStudentDialog" @close="clearStudenInfo">
                  <el-form ref="form" :model="studentInfo" label-width="80px">
                      <el-form-item label=":">
                          <img :src="studentInfo.headerUrl" class="student-img">
                      </el-form-item>

                      <el-form-item label=":">
                          <el-input v-model="studentInfo.name"></el-input>
                      </el-form-item>
                      <el-form-item label="">
                          <el-radio v-model="studentInfo.sex" label=""></el-radio>
                          <el-radio v-model="studentInfo.sex" label=""></el-radio>
                      </el-form-item>
                      <el-form-item label="">
                          <el-input v-model="studentInfo.phone"></el-input>
                      </el-form-item>
                      <el-form-item label="" >
                          <el-input v-model="studentInfo.password"></el-input>
                      </el-form-item>
                      <el-form-item label="">
                          <el-input v-model="studentInfo.cdCard"></el-input>
                      </el-form-item>
                      <el-form-item label="qq">
                          <el-input v-model="studentInfo.qq"></el-input>
                      </el-form-item>
                    
                      <el-form-item>
                          <el-button type="primary" @click="updateStudentInfo"></el-button>
                          <el-button @click="closeUpdateStudentDialog"> </el-button>
                      </el-form-item>
                  </el-form>
            </el-dialog>


        <el-dialog title="" :visible.sync="showStudentApplyListDialog" @close="clearApplyInfo" width="80%">
              <el-table
              height="500"
              :data="StudentApplyList"
              style="width: 100%;margin-bottom:50px;">

              <el-table-column
              label=""
              width="180">
              <template slot-scope="scope">
                  <span >{{ scope.row.scName }}</span>
              </template>
              </el-table-column>

              <el-table-column
              label=""
              width="150">
              <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                      <el-tag size="medium">{{ scope.row.teacher.name }}</el-tag>
                  </div>
              </template>
              </el-table-column>

              <el-table-column
              label=""
              width="120">
              <template slot-scope="scope">
                  <span>{{ scope.row.college.collegeName}}</span>
              </template>
              </el-table-column>

              <el-table-column
              label=""
              width="200">
              <template slot-scope="scope">
                  <i class="el-icon-time"></i>
                  <span>{{ scope.row.createTime }}</span>
              </template>
              </el-table-column>

             <el-table-column
              label=""
              width="120">
              <template slot-scope="scope">
                  <div slot="reference" class="name-wrapper">
                      <el-tag v-if="scope.row.status==1" type="success" size="medium"></el-tag>
                      <el-tag v-else type="danger" size="medium"></el-tag>
                  </div>
              </template>
             </el-table-column>

             <el-table-column
              label=""
              width="150">
              <template slot-scope="scope">
                <form :action="scope.row.applyFile">
                    <intput type="hidden" name="Content-Disposition" :value="'attachment;filename='+scope.row.applyFile.slice(54)"></intput>
                    <button  style="height:25px"  type="primary"></button>
                </form>
              </template>
             </el-table-column>

              <el-table-column label="">
                <template slot-scope="scope">
                      <el-button type="success" @click="updateStatus(scope.row.id)" size="mini" :disabled="!(teacher.id==scope.row.teacher.id)"></el-button>
                </template>
              </el-table-column>
            </el-table>
        </el-dialog>

        <el-dialog title="" :visible.sync="showStudentExamDialog" @close="clearExamInfoList" width="80%">
            <div style="margin-bottom:10px">
              <img :src="studentInfo.headerUrl" class="header-img">
              <span>{{studentInfo.name}}</span> 
            </div>
            
            <el-select v-model="scId" placeholder="" @change="scChange">
              <el-option
                v-for="item in finalScInfoList"
                :key="item.id"
                :label="item.scName"
                :value="item.id">
              </el-option>
            </el-select>

            <el-select v-model="epId" placeholder="" @change="epChange">
              <el-option
                v-for="item in examProjectList"
                :key="item.id"
                :label="item.projectName"
                :value="item.id">
              </el-option>
            </el-select>

               <el-button
                size="mini" type="primary"  :disabled="!epId" style="margin-left:10px"
                @click="addExam"></el-button>

             <el-table
              height="500"
              :data="scExamList"
              style="width: 100%;margin-bottom:50px;">

              <el-table-column
              label=""
              width="180">
              <template slot-scope="scope">
                  <span >{{""+scope.row.examIndex+""}}</span>
              </template>
              </el-table-column>

              <el-table-column
              label=""
              width="180">
              <template slot-scope="scope">
                  <span >{{ scope.row.score }}</span>
              </template>
              </el-table-column>

              <el-table-column
              label=""
              width="180">
              <template slot-scope="scope">
                  <span >{{ scope.row.description }}</span>
              </template>
              </el-table-column>

              <el-table-column
                label="">
                <template slot-scope="scope">

                    <el-button
                    size="mini"
                    @click="updateExam(scope.row)"></el-button>

                    <el-button
                    size="mini"
                    type="danger"
                    @click="deleteExam(scope.row.id)"></el-button>

                </template>
                </el-table-column>

            </el-table>
           
        </el-dialog>

        <el-dialog :title="isAddExamInfo?'':''" :visible.sync="addOrUpdateExamDialog" @close="clearExamInfo" width="80%">

          <el-form  :model="ExamInfo"  label-width="120px" label-position="right" style="width:600px">
            <el-form-item label=":">
              <el-input v-model="ExamInfo.examIndex" :disabled="true"></el-input>
            </el-form-item>

            <el-form-item label=":">
              <el-input v-model="ExamInfo.score" placeholder="0~100"></el-input>
            </el-form-item>

            <el-form-item label=":" >
              <el-input v-model="ExamInfo.description"></el-input>
            </el-form-item>

            <el-form-item>
              <el-button :type="isAddExamInfo?'primary':'success'"  @click="addOrUpdateSubmit">{{isAddExamInfo?'':''}}</el-button>
            </el-form-item>
          </el-form>
        </el-dialog>




          </el-card>
</div>
</template>

<script>
import request from "../../../utils/request"
import  cookie from 'js-cookie'
export default {
//import 
components:{},
props:{},
data(){
//
return{
  teacher:{},
  classStudentList:[],
  college:{},
  showUpdateStudentDialog:false,
  studentInfo:{},

  showStudentApplyListDialog:false,
  StudentApplyList:[],

  showStudentExamDialog:false,
  scId:"",
  epId:"",
  teacherScInfoList:[],
  examProjectList:[],
  scExamList:[],

  studentApplyIds:[],
  finalScInfoList:[],

  ExamInfo:{},
  addOrUpdateExamDialog:false,
  isAddExamInfo:false,


};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
  getClassStudentList(){
      request({
        url:`/student/classStudentList/${this.teacher.aclass.id}`,
        method:'get'
      }).then(res=>{
          this.classStudentList=res.data.classStudentList;
          this.college=res.data.college;
      }).catch(err=>{
      })
  },
  updateStudentInfo(){
       let {id,name,sex,phone,password,cdCard,qq}=this.studentInfo
            
            request({
                url:'/student/update',
                method:'put',
                data:{
                    id,name,sex,phone,password,cdCard,qq
                }
                    
            }).then(res=>{
              if(res.code!=500){
                this.getClassStudentList();
                this.closeUpdateStudentDialog();
                 this.$message({
                    showClose: true,
                    message: '',
                    type: 'success'
                });
              }else{
                  this.$message({
                    showClose: true,
                    message: res.msg,
                    type: 'error'
                });
              }
               
            }).catch(err=>{
                console.log(err);
                
            })
  },
  openUpdateStudentDialog(studentId){
    this.showUpdateStudentDialog=true;
    request({
      url:`/student/get/${studentId}`,
      method:'get'
    }).then(res=>{
      this.studentInfo=res.data.studentInfo
    }).catch(err=>{

    })
  },
  showStudentApplyList(studentId){
      this.showStudentApplyListDialog=true;
      request({
        url:`/scApply/studentScApplyList/${studentId}`,
        method:'get'
      }).then(res=>{
        this.StudentApplyList=res.data.ScApplyInfo
      }).catch(err=>{
          
      })
  },
  Delete(studentId){
    this.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
            request({
              url:`/student/delete/${studentId}`,
              method:'delete'
            }).then(res=>{
              this.getClassStudentList();
            }).catch(err=>{
            })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
  });
  },
  getteacherScInfoList(){
    request({
      url:`/scInfo/teacherScInfoList/${this.teacher.id}`,
      method:'get'
    }).then(res=>{
      this.teacherScInfoList=res.data.teacherScInfoList;
    }).catch(err=>{
      console.log(err);
    })
  },
  doStudentExam(studentId){
  request({
        url:`/scInfo/studentScInfoList/${studentId}`,
        method:'get'
    }).
    then(res=>{
      this.studentApplyIds=res.data.studentScInfoList
      for(let i=0;i<this.studentApplyIds.length;i++){
        for(let j=0;j<this.teacherScInfoList.length;j++){
          if(this.studentApplyIds[i].id==this.teacherScInfoList[j].id){
            this.finalScInfoList.push(this.teacherScInfoList[j])
          }
        }
      }
    }).catch(err=>{
        console.log(err)
    })


    request({
      url:`/student/get/${studentId}`,
      method:'get'
    }).then(res=>{
      this.studentInfo=res.data.studentInfo
    }).catch(err=>{
        console.log(err);
    })

    this.showStudentExamDialog=true;
  },
  getExamProjectList(scId){
    request({
      url:`/examProject/list/${scId}`,
      method:'get'
    }).then(res=>{
      this.examProjectList=res.data.examProjectList
    }).catch(err=>{
      console.log(err);
    })
  },
  scChange(scId){
    this.epId=""
    this.getExamProjectList(scId)
  },
  epChange(epId){
    this.scExamList=[];
    request({
      url:`/scExam/list/${this.studentInfo.id}/${this.scId}/${epId}`,
      method:'get'
    }).then(res=>{
      if(res.code!=500){
              this.scExamList=res.data.scExamList;
      }else{
         this.$message({
              showClose: true,
              message: '',
              type: 'warning'
          });
      }
    }).catch(err=>{
      console.log(err);
    })
  },
  updateExam(exam){
      this.isAddExamInfo=false;
      console.log(exam);
      this.addOrUpdateExamDialog=true;
      this.ExamInfo=exam;
  },
  addExam(){
      this.isAddExamInfo=true;
      this.addOrUpdateExamDialog=true;
      this.ExamInfo.examIndex=1;
      if(this.scExamList[this.scExamList.length-1]){
          this.ExamInfo.examIndex=this.scExamList[this.scExamList.length-1].examIndex+1;
      }
  },
  deleteExam(exId){
      console.log(exId);
      request({
        url:`/scExam/delete/${exId}`,
        method:'delete'
      }).then(res=>{
          if(res.code!=500){
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
              });
              this.epChange(this.epId);
            }else{
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'error'
              });
            }
      }).catch(error=>{
        console.log(error);
        
      })
  },
  addOrUpdateSubmit(){
      if(this.isAddExamInfo){
        this.ExamInfo.scId=this.scId;
        this.ExamInfo.studentId=this.studentInfo.id;
        this.ExamInfo.epId=this.epId;
        request({
          url:'/scExam/add',
          method:'post',
          data:this.ExamInfo
        }).then(res=>{
            if(res.code!=500){
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
              });
              this.epChange(this.epId);
            }else{
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'error'
              });
            }
        }).catch(err=>{
          console.log(err);
        })

      }else{
          request({
            url:'/scExam/update',
            method:'put',
            data:this.ExamInfo
          }).then(res=>{
            if(res.code!=500){
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
              });
            }else{
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'error'
              });
            }
          }).catch(err=>{
            console.log(err);
          })
      }
      this.addOrUpdateExamDialog=false;
  },
  closeUpdateStudentDialog(){
    this.showUpdateStudentDialog=false;
  },
  clearStudenInfo(){
    this.studentInfo={};
  },

  closeStudentApplyListDialog(){
    this.showStudentApplyListDialog=false;
  },
  clearApplyInfo(){
    this.StudentApplyList=[];
  },
  clearExamInfoList(){
    this.scId="",
    this.epId="",
    this.scExamList=[];
    this.studentInfo={};
    this.examProjectList=[];
    this.studentApplyIds=[];
    this.finalScInfoList=[];
  },
  clearExamInfo(){
    this.ExamInfo={};
  },
  
  updateStatus(scApplyId){
    request({
      url:`/scApply/approval/${scApplyId}`,
      method:'put'
    }).then(res=>{
        if(res.code!=500){
          this.$message({
            type: 'success',
            message: ''
          });  
        }else{
          this.$message({
            type: 'error',
            message: ''
          });  
        }
    }).catch(err=>{

    })
  }
},
// - this
created() {
    if(cookie.get("teacher")){
      let teacherInfo=cookie.get("teacher");
      if(teacherInfo){
        this.teacher = JSON.parse(teacherInfo)
      } 
    }
    this.getClassStudentList();
    this.getteacherScInfoList();
},
// - DOM
mounted() {

},
beforeCreate() {},// - 
beforeMount() {},// - 
beforeUpdate() {},// - 
updated() {},// - 
beforeDestroy() {},// - 
destroyed() {},// - 
activated() {},//keep-alive
};
</script>

<style scoped>
.elcard{
min-height:650px;
border-radius: 20px;
}
.header-img{
    height: 50px;
    width: 50px;
    border-radius: 25px;
    display: inline;
    vertical-align:middle;
    margin-right: 10px;
}
.student-img{
    height: 80px;
    width: 80px;
    border-radius: 40px;
}
</style>