<template>
<div style="min-height:500px">
    <div class="search-item">
        <el-form :inline="true" :model="Searchdata" class="demo-form-inline">
            <el-form-item>
                <el-input v-model="Searchdata.querykey" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
                <el-date-picker  el-date-picker type="date" placeholder="" v-model="Searchdata.startTime" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item>
                <el-date-picker  el-date-picker type="date" placeholder="" v-model="Searchdata.endTime" style="width: 100%;"></el-date-picker>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click="search"></el-button>
            </el-form-item>
        </el-form>
    </div>
    
    <div v-if="pagelist.scInfoList.length!=0" style="display:flex;flex-wrap: wrap;justify-content: space-evenly;">
        <div class="sc-item" v-for="item in pagelist.scInfoList" :key="item.id" >
            <div class="left">
                <div class="sc-name">
                    <a @click="lookDetails(item.id)">{{item.scName}}</a>
                </div>
                <div class="sc-description">
                    {{item.description}}
                </div>
                <div class="sc-link">
                    <el-link class="sc-link-a" type="primary" @click="lookDetails(item.id)"></el-link>
                </div>

                <!-- <div>:{{item.scSponsor}}</div> -->
                <div>:{{item.teacher.name}}</div>
                <div>:{{item.college.collegeName}}</div>
                <div>:{{item.startTime.slice(0,10)}}</div>
                <div>:{{item.endTime.slice(0,10)}}</div>
            </div>
        
            <div class="right">
                <img
                    :src="item.firstUrl?item.firstUrl:'http://img.tuguaishou.com/ips_templ_preview/w432_q100/95/ae/7a/lg_1680129_1585904883_5e86fcf332be3.jpg?auth_key=2241100800-0-0-4378beb02cacac2e53a7cbd87e5f2d31'"/>
            </div>
        </div>
        
        
     </div>
     <div v-else>
        <div></div>
     </div>
    <div>
      <div class="page-item">
            <el-pagination
            background
            layout="prev, pager, next"
            :total="pagelist.total"
            :current-page="pagelist.currentPage"
            :page-size="12"
            @current-change="currentChange"
            >
            </el-pagination>
        </div>
    </div>
</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../../utils/request"

export default {
//import 
components:{},
props:{},
data(){
//
return{
    Searchdata:{
        querykey:"",
        startTime:"",
        endTime:"",
    },
    SearchButtonIsChecked:false,
    pagelist:
        {
        total: 20,//
        pages: 4,//
        currentPage:1,//
        scInfoList:
        [
        {
            id:1,
            scName:"",
            description:"",
            scSponsor:"",
            teacher:{id:1,name:""},
            college:{id:1,collegeName:""},
            startTime:'2020-11-10',
            endTime:'2020-11-30'
        }
        ]},
    student:{},
};
},
//
computed: {

},
//data
watch: {

},
//
methods: {
        lookDetails(sc_id){
            this.$router.push(`/cptdetails/${sc_id}`)
        },
        getpagelistBypage(currentPage){
            if(!(this.Searchdata.querykey||this.Searchdata.startTime||this.Searchdata.endTime))// 
                    this.SearchButtonIsChecked=false;
             
            if(this.SearchButtonIsChecked){
                request({
                    url:'/scInfo/list',
                    params:{
                        currentPage,
                        querykey:this.Searchdata.querykey,
                        startTime:this.Searchdata.startTime,
                        endTime:this.Searchdata.endTime
                    },
                    method:'get'
                }).then(res=>{
                    this.pagelist=res.data
                }).catch(err=>{
                    console.log(err)
                })
            }else{
                   request({
                    url:'/scInfo/list',
                    params:{
                        currentPage
                    },
                    method:'get'
                }).then(res=>{
                    this.pagelist=res.data
                }).catch(err=>{
                    console.log(err)
                })
            }
        },
        currentChange(currentPage){
            this.getpagelistBypage(currentPage)
        },
        search(){
            this.SearchButtonIsChecked=true;
            request({
                    url:'/scInfo/list',
                    params:{
                        currentPage:1,
                        querykey:this.Searchdata.querykey,
                        startTime:this.Searchdata.startTime,
                        endTime:this.Searchdata.endTime
                    },
                    method:'get'
                }).then(res=>{
                    this.pagelist=res.data
                }).catch(err=>{
                    console.log(err)
                })
        }

},
// - this
created() {
    this.getpagelistBypage(1);
}
}
</script>

<style scoped>

.search-item form{
    height: 40px;
    margin-top: 10px;
}
.sc-item{
    width: 680px;
    display:flex;
    justify-content: space-evenly;
    min-height:250px;
    margin: 10px 0px;
    border-radius: 20px;
    box-sizing: border-box;
    border: 1px solid;
    padding: 10px;
}
.left{
    width:380px;
    box-sizing: border-box;
}
.sc-name{
    text-align: center;
    color: #666;
    font-size: 24px;
}
.sc-name a:hover{
cursor:pointer;
color: #5E79A2;
}
.sc-description{
    text-indent:2em;
    font-size: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
}

.sc-link{
  text-align: right;
  line-height: 16px;
}
.sc-link-a{
font-size: 16px;
margin-right: 5px;
}

.right{
    width: 300px;
}
.right img{
     width:300px;
     height:328px;
     border-radius: 10px;
}

.page-item{
    text-align: center;
    margin: 10px;
}


</style>