<template>
<div class="login-container">
    
    <div class="login-main-container">
        <div class="logo">
             <b></b>
        </div>
        <div class="form-main">
        <el-form :model="loginForm" :rules="rules" ref="ruleForm"  class="login-form" >
            <el-form-item prop="key">
                <el-input prefix-icon="iconfont icon-yonghu" v-model="loginForm.key" placeholder="" autofocus></el-input>
            </el-form-item>
            <el-form-item prop="password">
                <el-input prefix-icon="iconfont icon-mima" v-model="loginForm.password" placeholder="" show-password></el-input>
            </el-form-item>
            <el-form-item>
                <el-radio-group v-model="loginForm.type">
                <el-radio label=""></el-radio>
                <el-radio label=""></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
                  <el-button type="success" @click="showDialog"></el-button>
            </el-form-item>
        </el-form>
        </div>
    </div>

    <el-dialog title="" :visible.sync="showStudentRegisterDialog" modal :close-on-click-modal="false" @close="dialogclose">
             <el-form  :model="studentInfo" :rules="studentRules" ref="registerForm"  label-width="100px">
                <el-form-item label="" prop="headerUrl">
                    <el-avatar :src="studentInfo.headerUrl" style="height:80px;width:80px;vertical-align:middle;margin-right:10px;"></el-avatar>
                    <el-upload
                      style="display:inline"
                      name="resources"
                      action="http://localhost:9000/oss/upload"
                      :on-success="uploadFileSuccess"
                      :on-error="uploadFileError"
                      :on-remove="handleRemove"
                      multiple
                      accept=".jpg,.png"
                      :file-list="fileList"
                      >
                      <el-button size="small" type="primary"></el-button>
                      <div slot="tip" class="el-upload__tip" style="display:inline">jpg/png500kb</div>
                    </el-upload>
                </el-form-item>

                <el-form-item label="" prop="name">
                    <el-input v-model="studentInfo.name"></el-input>
                </el-form-item>
                <el-form-item label="" prop="sex">
                    <el-radio v-model="studentInfo.sex" label=""></el-radio>
                    <el-radio v-model="studentInfo.sex" label=""></el-radio>
                </el-form-item>
                <el-form-item label="" prop="phone">
                    <el-input v-model="studentInfo.phone" @input="phoneChange"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="phoneCode" style="max-width:500px"></el-input>
                    <el-button @click="sendPhoneCode" :disabled="!flag">{{phoneCodebtnText}}</el-button>
                </el-form-item>
                <el-form-item label=""  prop="password">
                    <el-input v-model="studentInfo.password"></el-input>
                </el-form-item>
                <el-form-item label="" prop="cdCard">
                    <el-input v-model="studentInfo.cdCard"></el-input>
                </el-form-item>
                <el-form-item label="qq" prop="qq">
                    <el-input v-model="studentInfo.qq"></el-input>
                </el-form-item>
               
                <el-form-item label="" prop="classId">
                    <el-select v-model="studentInfo.collegeId" @change="collegeChange">
                        <el-option :label="item.collegeName" :value="item.id" v-for="item in collegeList" :key="item.id" ></el-option>
                    </el-select>

                    <el-select v-model="studentInfo.classId">
                        <el-option :label="item.className" :value="item.id" v-for="item in classList" :key="item.id"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" @click="register('registerForm')"></el-button>
                </el-form-item>
             </el-form>
    </el-dialog>

</div>
</template>

<script>
import  cookie from 'js-cookie'
import request from "../../utils/request"
export default {
//import 
components:{},
props:{},
data(){
return{
    loginForm: {
          key: '13142211138',
          password: '11111111',
          type:'',
    },
    rules: {
          key: [
            { required: true, message: '', trigger: 'blur' },
            { min: 11, max: 11, message: '', trigger: 'blur' }
          ],
          password:[
            { required: true, message: '', trigger: 'blur' },
            { min: 8, max: 16, message: ' 8  16 ', trigger: 'blur' }
          ]
    },
    showStudentRegisterDialog:false,
    studentInfo:{
      name:'',
      sex:'',
      phone:'',
      password:'',
      cdCard:'',
      qq:'',
      headerUrl:'',
      collegeId:'',
      classId:'',
      status:1
    },
    phoneCode:'',//
    phoneCodebtnText:'',
    flag:false,//true
    studentRules: {
          headerUrl: [
            { required: true, message: '', trigger: 'change' },
          ],
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 8, message: ' 2  8 ', trigger: 'blur' }
          ],
          sex: [
            { required: true, message: '', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '', trigger: 'blur' },
            { min: 11, max: 11, message: '11', trigger: 'blur' }
          ],
          password:[
            {required: true, message: '', trigger: 'blur' },
            { min: 8, max: 16, message: '816', trigger: 'blur' }
          ],
          cdCard:[
            {required: true, message: '', trigger: 'blur' },
            { min: 8, max: 16, message: '1620', trigger: 'blur' }
          ],
          qq: [
            {  required: true, message: 'QQ', trigger: 'blur' },
            { min: 5, max: 14, message: 'QQ514', trigger: 'blur' }
          ],
          classId: [
            {  required: true, message: '', trigger: 'change' },
          ]
        },
    fileList:[],
    addInfoSuccess:false,
    collegeList:[],
    classList:[]
};
},
//
computed: {
},
//data
watch: {
  'loginForm.type'(){
    if(this.loginForm.type===""){
        this.loginForm.key='13142211138';
        this.loginForm.password='11111111';
    }else if(this.loginForm.type===""){
         this.loginForm.key='15874245519';
        this.loginForm.password='11111111';
    }
  }
},
//
methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if(valid){
          if(this.loginForm.type===''){

              request({
                url:'/login/student',
                method:'post',
                data:this.loginForm
              }).then(res=>{
                if(res.code!=500){
                  this.$message({
                    message:res.msg,
                    type:'success'
                  })
                  console.log(res.data);
                  cookie.set("student",res.data.student)
                  cookie.set("cpt-token",res.data.token);
                  if(this.$route.query &&this.$route.query.redirect){
                   window.location.href = this.$route.query.redirect;
                  }else{
                   window.location.href = "/";
                  }
                }else{
                  this.$message({
                    message:res.msg,
                    type:'error'
                  })
                }
              }).catch(err=>{
                console.log(err)
              })

          }else{

              request({
                url:'/login/teacher',
                method:'post',
                data:this.loginForm,
              }).then(res=>{
                  this.$message({
                    message:res.msg,
                    type:'success'
                  })
                  console.log(res.data);
                  cookie.set("teacher",res.data.teacher)
                  cookie.set("cpt-token",res.data.token);
                  cookie.set("isAdmin",res.data.teacher.isAdmin)
                  if(this.$route.query &&this.$route.query.redirect){
                    window.location.href = this.$route.query.redirect;
                  }else{
                    window.location.href = "/adminMain";
                  }
              }).catch(err=>{
                console.log(err)
              })
          }

        }else{
          console.log("err");
          return false;
        }
      });
    },
    resetForm(formName){
        this.$refs[formName].resetFields();
    },
    showDialog(){
      this.showStudentRegisterDialog=true;
      this.getCollegeAndClassList();
    },
    async uploadFileSuccess(response, file, fileList){
      if(this.fileList && this.fileList.length!=0){  // 
      let res=  await request({
          url:'/oss/delete',
          method:'delete',
          params:{
              ObjectName:this.fileList[0].ObjectName
          }
      })
       if(res.code!=500){
          this.studentInfo.headerUrl='';
          this.fileList.shift()
        }
      }
      let name=response.data.urlList[0].fileName;//
      let ObjectName=response.data.urlList[0].resourceName;//oss
      let url=response.data.urlList[0].resourceUrl;//oss
      this.fileList.push({url,name,ObjectName})
      this.studentInfo.headerUrl=url;
    },
    uploadFileError(err, file, fileList){
        this.$refs.upload.clearFiles(); //
        this.$message({  //
          type: 'error',
          message: '',
          center: true,
          duration: 1000
         });
    },
    handleRemove(file, fileList) {
      //
      request({
        url:'/oss/delete',
        method:'delete',
        params:{
            ObjectName:file.ObjectName
        }
      }).then(res=>{
      }).catch(err=>{
      })
      this.fileList.pop();
      this.studentInfo.headerUrl='';
    },
    dialogclose(){
      if(this.fileList.length!=0&&!this.addInfoSuccess){
        request({
          url:'/oss/delete',
          method:'delete',
          params:{
              ObjectName:this.fileList[0].ObjectName
          }
      }).then(res=>{}).catch(err=>{})
      }
      this.fileList=[];
      this.collegeList=[];
      this.classList=[];
      this.addInfoSuccess=false;
      this.studentInfo={
        name:'',
        sex:'',
        phone:'',
        password:'',
        cdCard:'',
        qq:'',
        headerUrl:'',
        collegeId:'',
        classId:'',
        status:1
      };
      this.studentPhoneCode='',
      this.phoneCode='',
      this.phoneCodebtnText="",
      this.flag=false,
      clearInterval(this.timer)
      this.$refs['registerForm'].resetFields();
    },
    getCollegeAndClassList(){
        // 
        request({
            url:`/college/collegeList`,
            method:'get',
        }).then(res=>{
            this.collegeList=res.data.collegeList
            this.refreshClassList()
        }).catch(err=>{
            console.log(err)
        })
    },
    collegeChange(v){
        this.refreshClassList();
        this.studentInfo.classId=this.classList[0].id
    },
    refreshClassList(){
          for(let i=0;i<this.collegeList.length;i++){
            if(this.collegeList[i].id==this.studentInfo.collegeId){
                  this.classList=this.collegeList[i].classList;
                break;
            }
        }
    },
    register(formName){
       this.$refs[formName].validate((valid) => {
        if(valid){
            request({
              url:'/student/add',
              method:'post',
              data:this.studentInfo,
              params:{code:this.phoneCode}
            }).then(res=>{
              if(res.code!=500){
                this.$message({
                  message: res.msg,
                  type: 'success'
                });
                this.addInfoSuccess=true;
                this.showStudentRegisterDialog=false;
              }else{
                this.$message({
                  message: res.msg,
                  type: 'error'
                });
              }
            }).catch(err=>{
            })
      }else{
        return false;
      }})
    },
    phoneChange(){
        if(this.studentInfo.phone && this.studentInfo.phone.length==11){
          this.flag=true;
        }else{
          this.flag=false;
        }
    },
    sendPhoneCode(){
          if(!this.flag){
            return;
          }
          request({
                url:`/sms/send/${this.studentInfo.phone}`,
                method:'get'
          }).then(res=>{
              this.studentPhoneCode=res.data.code;
          }).catch(err=>{
              console.log(err);
          })

          this.flag=false;
          let tiemcount=60;
          this.timer = setInterval(()=>{
            this.phoneCodebtnText=tiemcount+"s";
            if(tiemcount<1){
              this.flag = true
              this.phoneCodebtnText=''
              clearInterval(this.timer)
            }else{
              tiemcount--;
            }
          },1000)
    },
},
// - this
created() {
}
};
</script>

<style scoped>
.login-container{
    position: relative;
    height: 100%;
    /* width: 100%; */
    /* height: 100%; */
    background-color: #d2d6de;
}
.login-main-container{
  width: 360px;
  position: absolute;
  left:50%;
  top:50%;
  transform: translate(-50%, -80%);
}
.logo{
    font-size: 35px;
    text-align: center;
    font-weight: 300;
    background-color: #d2d6de;
    box-sizing: border-box;
    padding: 20px;
}
.form-main{
  background: white;
  padding: 40px;
  border-radius: 20px;
}
</style>